import Header from './Header';
import Footer from './Footer';

import 'bootstrap/dist/css/bootstrap.min.css';

import { Container, Col, Row } from 'react-bootstrap';

const siteName = 'MMLiterature';

const Layout = props => (
  <div>
    <Header />
    {props.children}
    <Footer />
    <style jsx>{`
      body {
        background: #ffffff;
      }
      .mainContainer {
        padding: 0px;
        border: 0px;
        width: calc(100% - 60px);
        height: 100%;
        max-width: 1740px;
        position: relative;
        display: block;
        margin: 0px auto;
      }

      @media only screen and (min-width: 1755px) {
        .mainContainer {
          margin: 0px auto;
        }
      }
    `}</style>
  </div>
);

export default Layout;
